import React from "react";
import { Paper, Divider } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../Theme/themes";
import Slider from "../Slider";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

const SliderExample = ({ brand, mode }) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="sliders">
                <Typography variant="h3">Sliders</Typography>
                <br />
                <Divider />
                <br />
                <Grid container>
                    <Grid item xs={12} lg={6}>
                        <Slider showValue values={[100]} min={0} max={100} />
                        <Slider showValue values={[0, 100]} min={0} max={100} />
                        <Slider showValue values={[0, 500]} min={0} max={500} prefix="R$" />
                        <Slider showValue values={[0, 24]} min={0} max={24} sufix="h" />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

storiesOf("Components/Slider", module)
    .add("Default", (args) => <SliderExample {...args}/>, {
        args: {
            brand: "CVC",
            mode: "root",
        },
        argTypes: {
            brand: {
                type: "select",
                options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
            },
            mode: {
                type: "select",
                options: ["root", "darkBg"]
            },
        }
    });
